<template>
  <div class="csmXq">
    <meta
      name="viewport"
      content="width=device-width,initial-scale=1.0, minimum-scale=1.0, maximum-scale=1.0, user-scalable=no"
    />
    <div class="csmXq_back">
      <van-cell-group class="csmXq_jbxx" center :border="false" inset>
        <van-cell
          title=""
          :value="xcmXqLIST.place_name"
          class="vanCellClass"
        />
        <van-cell
          title=""
          :value="xcmXqLIST.real_name"
          class="vanCellClass"
        />
        <van-cell title="" class="vanCellClass">
          <span>
            {{ xcmXqLIST.id_card | IDCARDFILTE(xcmXqLIST.id_card) }}
          </span>
        </van-cell>
      </van-cell-group>
    </div>
    <div
      style="
        margin-top: 4.6875rem;
        font-size: 1.875rem;
        margin-bottom: 0.9375rem;
      "
    >
      {{ time }}
    </div>
    <div class="jkmImg">
      <img :src="xcmXqLIST.qrcode" alt="" class="hunder" />
    </div>
    <div class="hxjc">
      <van-cell-group class="hxjcBox" center inset>
        <van-cell
          title=""
          title-class="hxjcTitle"
          value-class="hxjcValue"
        >
          <template #label>
            <div v-if="xcmXqLISThsjc.hsjc_time" style="margin-top: 1.5625rem">
              <div class="jcTime">
                <div></div>
                <div style="word-break: break-all; margin: 0.4375rem 0">
                  {{ xcmXqLISThsjc.hsjc_time }}
                </div>
              </div>
              <div class="jcJiGo">
                <div></div>
                <div
                  style="
                    width: 9.375rem;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 2;
                    overflow: hidden;
                  "
                >
                  {{ xcmXqLISThsjc.hsjc_jcjg }}
                </div>
              </div>
            </div>
            <div v-else></div>
          </template>
          <template #default>
            <template v-if="!xcmXqLISThsjc.hsjc_time"
              ><span class="hxjcValue"></span>
            </template>
            <template v-else
              ><span>{{ xcmXqLISThsjc.hsjc_result }}</span>
            </template>
          </template>
        </van-cell>
      </van-cell-group>
      <div class="right">
        <div class="xgym">
          <div class="title_sm"></div>
          <div v-if="xcmXqLISTymjz">
            <div class="zt_va" v-if="xcmXqLISTymjz.vaccination === 1">
              
            </div>
            <div class="zt_va" v-if="xcmXqLISTymjz.vaccination === 0">
              
            </div>
            <div class="zt_va" v-if="!xcmXqLISTymjz.vaccination">
              {{ noMsg }}
            </div>
          </div>
        </div>
        <div class="ymjz">
          <div class="title_sm"></div>
          <div class="zt_va" v-if="xcmXqLISTjkm.jkm_mzt">
            {{ xcmXqLISTjkm.jkm_mzt }}
          </div>
          <div class="zt_va" v-else>{{ noMsg }}</div>
        </div>
      </div>
    </div>
    <van-cell class="hxjcCell">
      <template #default>
        <div class="travelCard">
          <div
            class="xckTitle"
            style="font-size: 1.25rem; margin-bottom: 1.25rem"
          >
            {{ xcmXqLISTXcm.phone | phone(xcmXqLISTXcm.phone) }}
          </div>
          <div class="xckImg">
            <template v-if="xcmXqLISTXcm.xcm_result === '1'">
              <img src="../assets/img/greenCxk.png" class="hunder" alt="" />
            </template>
            <template v-if="xcmXqLISTXcm.xcm_result === '2'">
              <img src="../assets/img/yellowXcK.png" class="hunder" alt="" />
            </template>
            <template v-if="xcmXqLISTXcm.xcm_result === '3'"></template>
          </div>
          <div class="xckTujinCity" style="font-weight: bolder">
            <template v-if="xcmXqLISTXcm.xcm_result === '1'">
              14<span style="color: #53a46b"></span
              >
            </template>
            <template v-if="xcmXqLISTXcm.xcm_result === '2'">
              14<span style="color: red"></span>
            </template>
            <template v-if="xcmXqLISTXcm.xcm_result === '3'">
              
            </template>
          </div>
          <div class="xckTujinCity" style="font-weight: bolder">
            <template v-if="xcmXqLISTXcm.xcm_result === '2'">
              <span style="color: #53a46b"></span>
            </template>
            <template v-if="xcmXqLISTXcm.xcm_result === '1'">
              <span style="color: #ffd700"
                ></span
              >
            </template>
            <template v-if="xcmXqLISTXcm.xcm_result === '3'">
              <span style="color: red"></span>
            </template>
          </div>
        </div>
      </template>
    </van-cell>
  </div>
</template>

<script>
import {
  postPlacedeClareResult, placedeclareDetail
} from '@/common/api/api'
import cookies from '../lib/util.cookie'
import { Toast } from 'vant'
export default {
  data () {
    return {
      noMsg: '',
      xcmXqLIST: {},
      xcmXqLISTymjz: {},
      xcmXqLISTXcm: {},
      xcmXqLISThsjc: {},
      xcmXqLISTjkm: {},
      time: '',
      loadingIF: false,
      msg: '',
      djsNow: 0,
      jbxxList: [{
        title: '',
        value: ''
      }, {
        title: '',
        value: ''
      }, {
        title: '',
        value: ''
      }]
    }
  },
  created () {
    console.log(this.$route.params.id)
    let sign = cookies.get('sign')
    console.log(sign)
    // if (typeof xcmXqLIST === 'string' && xcmXqLIST) {
    //   this.xcmXqLIST = JSON.parse(xcmXqLIST)
    // } else {
    //   if (this.$route.params.id) {
    //     this.getCsmXq(this.$route.params.id)
    //   }
    // }
    if (sign) {
      this.getCsmXq(sign)
    }
  },
  destroyed () {
    clearTimeout(this.nowTimeOut)
    clearTimeout(this.time)
  },
  mounted () {
    this.getNowTime()
  },
  methods: {
    async getCsmXq (sign) {
      await this.getCsmXqDetail(sign)
      await this.getCsmXqXcm(sign)
      await this.getCsmXqJkm(sign)
      await this.getCsmXqHsjc(sign)
      await this.getCsmXqYmjz(sign)
    },
    getNowTime () {
      const this_ = this
      var d = new Date()
      var year = d.getFullYear()
      var month = d.getMonth() + 1
      var day = d.getDate()
      var hour = d.getHours()
      var minute = d.getMinutes()
      var minute1 = '0' + minute
      var minute2 = minute1.substring(minute1.length - 2)
      var second = d.getSeconds()
      var secondTmp = '0' + second
      var secondNew = secondTmp.substring(secondTmp.length - 2)
      var s = year + '-' + month + '-' + day + '   ' + hour + ':' + minute2 + ':' + secondNew
      this.time = s
      this.nowTimeOut = setTimeout(() => {
        this_.getNowTime()
      }, 1000)
    },
    // 
    getCsmXqDetail (sign) {
      placedeclareDetail({
        sign
      }).then(res => {
        console.log(res)
        if (res.code === 200) {
          this.xcmXqLIST = res.data
        }
      })
    },
    // 
    getCsmXqXcm (sign) {
      const that = this
      postPlacedeClareResult({
        sign,
        type: 'xcm'
      }).then(res => {
        console.log(res)
        if (res.code === 200) {
          this.xcmXqLISTXcm = res.data
          this.loadingIF = false
          this.$forceUpdate()
        } else if (res.code === 4002) {
          this.msg = res.msg
          this.loadingIF = true
          this.$forceUpdate()
        } else {
        }
      })
    },
    // 
    getCsmXqJkm (sign) {
      const that = this
      postPlacedeClareResult({
        sign,
        type: 'jkm'
      }).then(res => {
        console.log(res)
        if (res.code === 200) {
          this.xcmXqLISTjkm = res.data
          this.loadingIF = false
          this.$forceUpdate()
          clearTimeout(this.setTImeJkm)
        } else if (res.code === 4002) {
          this.msg = res.msg
          this.loadingIF = true
          this.$forceUpdate()
          if (this.djsNow === 20) {
            clearTimeout(this.setTImeJkm)
            this.noMsg = ''
          } else {
            this.setTImeJkm = setTimeout(() => {
              that.getCsmXqJkm(sign)
              that.djsNow++
            }, 1000)
          }
        } else {
          clearTimeout(this.setTImeJkm)
        }
      })
    },
    // 
    getCsmXqHsjc (sign) {
      const that = this
      postPlacedeClareResult({
        sign,
        type: 'hsjc'
      }).then(res => {
        console.log(res)
        if (res.code === 200) {
          this.xcmXqLISThsjc = res.data
          this.loadingIF = false
          this.$forceUpdate()
          clearTimeout(this.setTImeHSjc)
        } else if (res.code === 4002) {
          this.msg = res.msg
          this.loadingIF = true
          this.$forceUpdate()
          if (this.djsNow === 20) {
            clearTimeout(this.setTImeHSjc)
          } else {
            this.setTImeHSjc = setTimeout(() => {
              that.getCsmXqHsjc(sign)
              that.djsNow++
            }, 1000)
          }
        } else {
          clearTimeout(this.setTImeHSjc)
        }
      })
    },
    // 
    getCsmXqYmjz (sign) {
      const that = this
      postPlacedeClareResult({
        sign,
        type: 'ymjz'
      }).then(res => {
        console.log(res)
        if (res.code === 200) {
          this.xcmXqLISTymjz = res.data
          this.loadingIF = false
          this.$forceUpdate()
          clearTimeout(this.setTImeymjz)
        } else if (res.code === 4002) {
          this.msg = res.msg
          this.loadingIF = true
          this.$forceUpdate()
          if (this.djsNow === 20) {
            clearTimeout(this.setTImeymjz)
          } else {
            this.setTImeymjz = setTimeout(() => {
              that.getCsmXqYmjz(sign)
              that.djsNow++
            }, 1000)
          }
        } else {
          clearTimeout(this.setTImeymjz)
        }
      })
    }
  }
}
</script>
<style >
.title > .van-button--primary {
  color: black;
}
.title > .van-button__icon {
  font-size: 2.2em;
  line-height: inherit;
}
.title > .van-cell__label {
  color: #666666 !important;
}
.hxjcBox .van-cell__title {
  min-width: 80% !important;
}
</style>
<style scoped>
.csmXq {
  min-height: 100vh;
  /* background: #565353; */
  display: flex;
  /* justify-content: center; */
  align-items: center;
  flex-direction: column;
  background: #e5efff;
  /* padding: 0 0.3125rem; */
}
.title {
  font-size: 1.125rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 3.125rem;
  color: #fff;
  background: #fff;
  /* margin-bottom: 1rem; */
}
.csmXq_back {
  width: 100%;
  height: 7.375rem;
  background: #4382ec;
  position: relative;
}
.csmXq_jbxx {
  /* width: 22.1875rem; */
  width: 95%;
  height: 8.9375rem;
  border-radius: 0.5rem;
  box-shadow: 0px 0px 0.5625rem 1px rgba(12, 12, 12, 0.11);
  position: absolute;
  top: 1.25rem;
  left: 0.625rem;
  background: #fff;
  padding: 10px;
  box-sizing: border-box;
}
.vanCellClass {
  /* padding-bottom: 1.5625rem; */
  height: 33%;
  display: flex;
  align-items: center;
}
.hxjc {
  margin-top: 0.9375rem;
  width: 95%;
  display: flex;
  flex-direction: row;
}
.hxjcBox {
  /* width: 12.6875rem; */
  width: 80%;
  height: 9.0313rem;
  background: #ffffff;
  border-radius: 0.5rem;
  box-shadow: 0px 0px 0.5625rem 1px rgba(12, 12, 12, 0.11);
  padding: 0.9375rem;
}
.hxjcTitle {
  font-size: 1.0625rem;
  font-weight: bold;
}
.hxjcValue {
  color: #4382ec;
  font-weight: 600;
  /* min-width: 20% !important; */
}
.hxjcValueRed {
  color: red;
  font-weight: 600;
}
.jcTime .jcJiGo {
  font-size: 0.875rem;
}
.van-cell {
  padding: 0;
}
.title_sm {
  font-size: 0.875rem;
  font-weight: bold;
}
.zt_va {
  color: #57a16e;
  font-weight: bold;
}
.xgym,
.ymjz {
  width: 7.5625rem;
  height: 5.0625rem;
  background: #ffffff;
  border-radius: 0.5rem;
  box-shadow: 0px 0px 0.5625rem 2.1px rgba(12, 12, 12, 0.11);
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 0.625rem;
}
.right {
  margin-left: 0.625rem;
}
.travelCard {
  /* width: 95%; */
  background: #fff;
  /* height: 15.2813rem; */
  border-radius: 0.5rem;
  box-shadow: 0px 0px 0.5625rem 2.1px rgba(12, 12, 12, 0.11);
  display: flex;
  align-items: center;
  padding: 0.625rem;
  box-sizing: border-box;
  flex-direction: column;
}
.hxjcCell {
  background: #e5efff;
  width: 100%;
  padding: 0 0.5125rem;
  box-sizing: border-box;
}
.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.block {
  width: 300px;
  height: 120px;
  /* background-color: #fff; */
  display: flex;
  align-items: center;
  justify-content: center;
}
.xckImg {
  width: 8.4375rem;
  height: 8.5rem;
}
.hunder {
  width: 100%;
  height: 100%;
}
.jkmImg {
  width: 13.75rem;
  height: 13.75rem;
}
</style>



