axios.interceptors.response.use(
  response => {
    return response
  },
  // eslint-disable-next-line handle-callback-err
  error => {
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          error.message = '(400)'
          break
        case 401:
          error.message = '(401)'
          break
        case 403:
          error.message = '(403)'
          break
        case 404:
          error.message = '(404)'
          break
        case 408:
          error.message = '(408)'
          break
        case 500:
          error.message = '(500)'
          break
        case 501:
          error.message = '(501)'
          break
        case 502:
          error.message = '(502)'
          break
        case 503:
          error.message = '(503)'
          break
        case 504:
          error.message = '(504)'
          break
        case 505:
          error.message = 'HTTP(505)'
          break
        default:
          error.message = `(${error.response.status})!`
      }
    } else {
      error.message = '!'
      // removeToken()
    }
    Toast.fail(error.message)
    return Promise.reject(error)
  }
)
export function get (url, params) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
      params: params
    }).then(res => {
      resolve(res.data)
    }).catch(err => {
      reject(err.data)
    })
  })
}
export function post (url, params) {
  return new Promise((resolve, reject) => {
    axios.post(url, params)
      // axios.post(url, QS.stringify(params))
      .then(res => {
        resolve(res.data)
      })
      .catch(err => {
        reject(err.data)
      })
  })
}
export function deleteIt (url, params) {
  return new Promise((resolve, reject) => {
    axios.delete(url)
      .then(res => {
        resolve(res.data)
      })
      .catch(err => {
        reject(err.data)
      })
  })
}

export function put (url, params) {
  return new Promise((resolve, reject) => {
    axios.put(url, params)
      .then(res => {
        resolve(res.data)
      })
      .catch(err => {
        reject(err.data)
      })
  })
}
export function post1 (url, params) {
  return new Promise((resolve, reject) => {
    // axios.post(url, params)
    axios.post(url, QS.stringify(params))
      .then(res => {
        resolve(res.data)
      })
      .catch(err => {
        reject(err.data)
      })
  })
}

export function post2 (url, params) {
  return new Promise((resolve, reject) => {
    // axios.post(url, params)
    axios.post(url + '?' + QS.stringify(params))
      .then(res => {
        resolve(res.data)
      })
      .catch(err => {
        reject(err.data)
      })
  })
}
export function post3 (url, str) {
  return new Promise((resolve, reject) => {
    // axios.post(url, params)
    axios.post(url + str)
      .then(res => {
        resolve(res.data)
      })
      .catch(err => {
        reject(err.data)
      })
  })
}



